<template>
  <div class="topbar">
    <a href="/" class="toplogo"></a>
    <el-menu theme="light" default-active="1" class="el-menu-demo" mode="horizontal" @select="handleSelect">
      <el-menu-item index="1">首页</el-menu-item>
      <el-menu-item index="2"><a href="#">游戏中心</a></el-menu-item>
      <el-menu-item index="3">用户中心</el-menu-item>
      <el-menu-item index="4">礼包中心</el-menu-item>
      <el-menu-item index="4">充值中心</el-menu-item>
      <el-menu-item index="4">客服中心</el-menu-item>
      <el-menu-item index="4">玩家论坛</el-menu-item>
    </el-menu>
  </div>
</template>
<script>
  export default {
    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      }
    }
  }
</script>
<style>
  .topbar {
    position: relative;
    width: 1260px;
    margin: 0 auto;
    height: 100px;
    overflow: visible;
  }
  .topbar .toplogo {
    position: relative;
    float: left;
    width: 191px;
    height: 58px;
    background-image: url(https://nie.res.netease.com/nie/gw/15v1/img/logo1_1c91104.png) no-repeat;
    top: 21px;
    display: block;
  }
  .el-menu--horizontal .el-menu-item {
    position: relative;
    float: left;
    margin: 0 25px;
    font-size: 16px;
    color: #888;
    height: 100px;
    line-height: 100px;
  }
  .el-menu {
    background: #fff;
  }
  .el-menu--horizontal .el-menu-item:hover, .el-menu--horizontal .el-submenu__title:hover {
    background-color: transparent;
  }
 .el-menu-item a {
   text-decoration: none;
 }
</style>



