<template lang="html">
  <div class="friend f-cb">
    <p class="title"><span>友情链接</span></p>
    <div class="friend-body">
      <div class="link-box" v-for="(friend,index) in friends">
        <a href="#">
          <span class="txt1">{{friend.title}}</span>
          <br>
          <span class="txt2">{{friend.txt}}</span>
        </a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  // props: ['friends'],
  data: function () {
    return {
      friends: [
        {title: '网易MuMu', txt: '电脑玩手游官方模拟器'},
        {title: '网易MuMu', txt: '电脑玩手游官方模拟器'},
        {title: '网易MuMu', txt: '电脑玩手游官方模拟器'},
      ]
    }
  }
}
</script>

<style lang="css">
  .friend {
    background: #fff;
    padding-bottom: 20px;
  }
  .friend-body {
    padding: 0 40px;
  }
  .link-box a {
    position: relative;
    float: left;
    width: 150px;
    padding: 10px 0;
    margin-right: 2px;
    margin-bottom: 2px;
    background: #e3e3e3;
    text-align: center;
    text-decoration: none;
  }
  .link-box a:hover{
    background: #20a0ff;
    color: #fff;
  }
  .link-box a:hover .txt1,
  .link-box a:hover .txt2 {
    color: #fff;
  }
  .link-box .txt1 {
    color: #222;
    font-size: 16px;
    line-height: 30px;
    text-align: center;
  }
  .link-box .txt2 {
    color: gray;
    font-size: 12px;
    line-height: 30px;
    text-align: center;
  }
</style>



